
.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu a {
    font-size: 1.2rem; 
    font-weight: 500;
}

/* Styling for the header */
.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
     z-index: 10;
}
.Header-bg
{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}
.hamburger{
    display: none;
}
/* Media query for screens with a width of 1040 pixels or less */
@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgb(0, 0, 0);
        position: absolute;
        top: 0;
        left: -100%; 
        z-index: -3;  
        transition: 0.3s;
    }

    .nav-menu.active
    {
        left:0;
    }

    .nav-menu li {
        padding: 1rem 0; 
    }

    .nav-menu a {
        font-size: 2rem; 
        color: white; /* Added color to the links */
        text-decoration: none; /* Removed underlines from links */
    }
    .hamburger
    {
        display: initial;
    }
}
